/* CHATBOX
=============== */

.chatbox {
  position: fixed;
  bottom: 2%;
  right: 1%;
  z-index: 123456;
}

/* CONTENT IS CLOSE */

.chatbox__support {
  display: none;
  flex-direction: column;
  background: #f9f9f9;
  /* width: 470px; */
  height: 570px;
  z-index: -123456;
  opacity: 0;
  transition: all .5s ease-in-out;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  --primaryGradientChat: linear-gradient(93.12deg, #581B98 0.52%, #9C1DE7 100%);
  --secondaryGradientChat: linear-gradient(268.91deg, #581B98 -2.14%, #9C1DE7 99.69%);
  --primaryBoxShadowChat: 0px 10px 15px rgba(0, 0, 0, 0.1);
  --secondaryBoxShadowChat: 0px -10px 15px rgba(0, 0, 0, 0.1);
  --primary: #581B98;
}

/* CONTENT ISOPEN */

.chatbox--active {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  z-index: 123456;
  opacity: 1;
  display: flex;
}

/* BUTTON */

.chatbox__button {
  text-align: right;
}

.send__button {
  padding: 6px;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

/* HEADER */

.chatbox__header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: orange;
}

/* MESSAGES */

.chatbox__messages {
  margin-top: auto;
  display: flex;
  overflow-y: scroll;
  flex-direction: column-reverse;
}

.messages__item {
  background: orange;
  max-width: 60.6%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.messages__item--operator {
  margin-left: auto;
}

.messages__item--visitor {
  margin-right: auto;
}

/* FOOTER */

.chatbox__footer {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
}

/* HEADER */

.chatbox__header {
  background: var(--primaryGradientChat);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 15px 20px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  box-shadow: var(--primaryBoxShadowChat);
}

.chatbox__image--header {
  border-radius: 50%;
  width: 50px;
  margin-right: 10px;
}

.biva__image{
  border-radius:50%;
}

.chatbox__heading--header {
  font-size: 1.2rem;
  color: white;
}

.chatbox__description--header {
  font-size: .9rem;
  color: white;
}

/* Messages */

.chatbox__messages {
  padding: 0 20px;
}

.messages__item {
  margin-top: 10px;
  background: rgba(160, 246, 216, 0.398);
  color: rgb(11, 11, 11);
  padding: 8px 12px;
  max-width: 70%;
}

.messages__item--visitor,
.messages__item--typing {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}

.messages__item--operator {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  border-bottom-left-radius: 20px;
}

/* FOOTER */

.chatbox__footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 20px 20px;
  background: var(--secondaryGradientChat);
  box-shadow: var(--secondaryBoxShadowChat);
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  margin-top: 20px;
}

.chatbox__footer input {
  width: 80%;
  border: none;
  padding: 10px 10px;
  border-radius: 30px;
  text-align: left;
}

.chatbox__send--footer {
  color: white;
}

.chatbox__button button,
.chatbox__button button:focus,
.chatbox__button button:visited {
  padding: 10px;
  background: white;
  border: none;
  outline: none;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  border-bottom-left-radius: 50px;
  box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

